.info-container {
  background-color: #cecece;
  border-radius: 2vw;
  width: 20vw;
  height: 30vw;
  padding: 2vw;
  position: fixed;
}

.sc-image {
  border-radius: 1vw;
  width: 100%;
  height: 10vw;
}

.white__square {
  z-index: 5;
  pointer-events: none;
  border: 1.6vw solid #fff;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  inset: 0% auto auto 0%;
}

@media screen and (width <= 479px) {
  .info-container {
    background-color: #cecece;
    border-radius: 4vw;
    width: 40vw;
    height: 60vw;
    padding: 4vw;
    position: fixed;
  }

  .sc-image {
    border-radius: 2vw;
    width: 100%;
    height: 20vw;
  }

  .info-container .title.small.lowercase {
    text-align: start;
  }
}

body {
  margin: 0;
  padding: 0;
}

ul {
  margin-block: 0;
  padding-inline-start: 0;
  list-style-type: none;
}

.nav {
  z-index: 4;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 5vw;
  height: 100vh;
  padding-top: 5vw;
  display: flex;
  position: fixed;
  top: 1.6vw;
  left: 1.6vw;
}

.nav__icon {
  cursor: pointer;
  color: #fff;
  background: #000;
  justify-content: center;
  align-items: center;
  width: 5vw;
  height: 5vw;
  display: flex;
  position: fixed;
  top: 1.6vw;
  left: 1.6vw;
}

.nav__icon img {
  width: 2vw;
  height: 2vw;
}

.nav__wrapper {
  justify-content: center;
  align-items: center;
  gap: 1vw;
  display: flex;
  transform: rotate(-90deg);
}

.nav__item {
  color: #000;
  text-transform: uppercase;
  letter-spacing: .1vw;
  font-family: sofia-pro, sans-serif;
  font-size: 1.4vw;
  font-weight: 700;
  line-height: 2vw;
  text-decoration: none;
}

.nav__icon.is-small {
  width: 3vw;
  height: 3vw;
}

.closed {
  display: none;
}



